import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, UrlTree } from '@angular/router';
import { Observable } from 'rxjs';

export interface CanComponentDeactivate {
	canDeactivate: (route?: ActivatedRouteSnapshot) => Observable<boolean> | Promise<boolean> | boolean | UrlTree;
}

@Injectable({
	providedIn: 'root',
})
export class ConfirmLeaveGuard {
	canDeactivate(component: CanComponentDeactivate, _route: ActivatedRouteSnapshot) {
		return component.canDeactivate ? component.canDeactivate(_route) : true;
	}
}
